import React, { useState } from "react";
import styles from "./LogIn.module.css";
import { useNavigate } from "react-router-dom";
import { comparePassword } from "../Components/Kryptering";
import { addDoc, collection } from "firebase/firestore";
import { db } from "../firebase-config";
const bcrypt = require("bcryptjs");

//Det hr ska finnas
// - formulr fr registrering
// - som ska skickas till db anvndare

function SignUp() {
  const [userEmail, setUserEmail] = useState("");
  const [userPassword, setUserPassword] = useState("");
  const [hashedEmail, setHashedEmail] = useState("");
  const [hashedPassword, setHashedPassword] = useState("");

  const navigate = useNavigate();
  const usersCollectionRef = collection(db, "users");

  function navToSignUp() {
    navigate("/signup");
  }

  async function handleLogin() {
    setHashedEmail(bcrypt.hash(userEmail));
    setHashedPassword(bcrypt.hash(userPassword));

    createUser();

    console.log(
      `${hashedEmail}: ${userEmail} & ${hashedPassword}: ${userPassword}`
    );
  }

  async function createUser() {
    await addDoc(usersCollectionRef, {
      email: hashedEmail,
      password: hashedPassword,
    });
  }

  function navToProfile() {
    navigate("/profile");
  }

  return (
    <main className={styles.wrapper}>
      <div>
        <label name="email">E-mail</label>
        <input
          type="text"
          name="email"
          placeholder="Enter E-mail..."
          onChange={(e) => {
            setUserEmail(e.target.value);
          }}
        />
        <label name="password">Password</label>
        <input
          type="password"
          name="password"
          placeholder="Enter Password"
          onChange={(e) => {
            setUserPassword(e.target.value);
          }}
        />
        <button onClick={handleLogin}>Log In</button>
        <p>Don't have an account?</p>
      </div>
      <button onClick={navToSignUp}>Sign up</button>
    </main>
  );
}

export default SignUp;
